import React, { Component } from 'react';
import PropTypes from 'prop-types';

import AppBar from 'material-ui/AppBar';
import Drawer from 'material-ui/Drawer';
import IconButton from 'material-ui/IconButton';
import MenuItem from 'material-ui/MenuItem';
import Divider from 'material-ui/Divider';

import NavigationClose from 'material-ui/svg-icons/navigation/close';
import ActionHome from 'material-ui/svg-icons/action/home';
import ActionPageView from 'material-ui/svg-icons/action/pageview';
import ImageGridOff from 'material-ui/svg-icons/image/grid-off';
import ImageGridOn from 'material-ui/svg-icons/image/grid-on';
import ImageColorLens from 'material-ui/svg-icons/image/color-lens';
import ContentLink from 'material-ui/svg-icons/content/link';
import ActionGrade from 'material-ui/svg-icons/action/grade';
import ActionHistory from 'material-ui/svg-icons/action/history';

import { grey500 } from 'material-ui/styles/colors';

import { Link } from 'react-router-dom';

class MenuDrawer extends Component {
  handleMenuClick = () => console.log('closed');

  render() {
    const styles = {
      MenuItemDisabled: {
        color: grey500,
        textTransform: 'uppercase',
      },
      DrawerHeader: {
        paddingLeft:'20px', 
        backgroundColor:'rgb(38,50,56)',
        height: 57,
      },
    }

    let navigationCloseIconLeft, 
        navigationCloseIconRight;

    let _navigationCloseIcon = (
      <IconButton>
        <NavigationClose
          onClick={() => this.props.onChange(false)}
        />
      </IconButton>
    )

    switch (true) {
      case (this.props.side):
        navigationCloseIconRight = _navigationCloseIcon;
        break;
      case (!(this.props.side)):
        navigationCloseIconLeft = _navigationCloseIcon;
        break;
      default:
        break;
    }
      
    return (
      <Drawer
        open={this.props.value}
        docked={false}
        onRequestChange={(val) => this.props.onChange(val)}
        openSecondary={this.props.side}
      >
        <AppBar
          title='FindPicture'
          showMenuIconButton={(!this.props.side)}
          iconElementLeft={navigationCloseIconLeft}
          iconElementRight={navigationCloseIconRight}
          style={styles.DrawerHeader}
        />
        <div className='DrawerBody'>
          <MenuItem 
            primaryText="Home" 
            leftIcon={<ActionHome />} 
            onClick={() => this.props.onChange(false)} 
            containerElement={<Link to=''/>}
          />
          <MenuItem 
            primaryText="Favorites" 
            leftIcon={<ActionGrade />} 
            onClick={() => this.props.onChange(false)} 
            // containerElement={<Link to='/recentlywatched'/>}
          />
          <MenuItem
            primaryText="Recently Watched"
            leftIcon={<ActionHistory />}
            onClick={() => this.props.onChange(false)}
            containerElement={<Link to='/recentlywatched' />}
          />
          <Divider />
          <MenuItem 
            primaryText="Grid" 
            disabled={true} 
            style={styles.MenuItemDisabled} 
          />
          <MenuItem 
            primaryText="on" 
            leftIcon={<ImageGridOn />} 
            onClick={this.handleMenuClick} 
          />
          <MenuItem 
            primaryText="off" 
            leftIcon={<ImageGridOff />} 
            onClick={this.handleMenuClick} 
          />
          <Divider />
          <MenuItem 
            primaryText="Style" 
            disabled={true} 
            style={styles.MenuItemDisabled} 
          />
          <MenuItem 
            primaryText="default" 
            leftIcon={<ImageColorLens />} 
            onClick={this.handleMenuClick} 
          />
          <MenuItem 
            primaryText="grey" 
            leftIcon={<ImageColorLens />} 
            onClick={this.handleMenuClick} 
          />
          <Divider />
          <MenuItem 
            primaryText="Go to Pixabay" 
            leftIcon={<ContentLink />} 
            onClick={this.handleMenuClick} 
          />
        </div>
      </Drawer>
    )
  }
}

MenuDrawer.propTypes = {
  value: PropTypes.bool.isRequired,
  onChange: PropTypes.func.isRequired,
  side: PropTypes.bool,
}

export default MenuDrawer


